import React from 'react'
import './style.scss'

export default (props: any) => {
  return (
    <div
      {...props}
      className={`${props.className} sfarm-btn ${props.size || ''} ${
        props.type || ''
      } ${props.disabled ? 'disabled' : ''}`}
      onClick={props.disabled ? () => true : props.onClick}
    >
      <span>{props.children}</span>
    </div>
  )
}
