import React, { useEffect, useState } from 'react'
import './style.scss'
import Button from '../Button'
import { ArrowUp } from '../Icons'
import {
  deposit,
  approveToken,
  checkDepositError,
  getNewReferralAddress
} from '../../service/deposit'
import {
  add,
  numberToWei,
  sub,
  weiToExactNumber,
  weiToNumber
} from '../../utils/helpers'
import Modal from '../Modal'
import { getText } from '../../utils/lang'

export default ({
  setVisible,
  visible,
  tokenChoosing,
  useWeb3React,
  useData,
  setData,
  xStorageClient,
  showLoading
}: {
  setVisible: any
  visible: any
  tokenChoosing: any
  useWeb3React: any
  useData: any
  setData: any
  xStorageClient: any
  showLoading: any
}) => {
  const { library: provider, account } = useWeb3React()
  const [amount, setAmount] = useState<any>(0)
  const [token, setToken] = useState<any>(null)
  const { contractABI, contractAddress } = useData()
  const data = useData()
  const [sending, setSending] = useState<boolean>(false)
  const [depositErr, setDepositErr] = useState<any>(false)
  const [newLzReferral, setNewLzReferral] = useState<string>('')

  useEffect(() => {
    window.addEventListener('keyup', (e: any) => {
      if (e.which === 27) {
        setVisible(false)
      }
    })
  }, [])

  useEffect(() => {
    if (tokenChoosing) {
      setToken(tokenChoosing)
    }
  }, [tokenChoosing, visible])

  useEffect(() => {
    if (token && visible) {
      setAmount(0)
      fetchNewReferral()
    }
  }, [token, visible])

  useEffect(() => {
    checkError()
  }, [amount])

  const fetchNewReferral = async () => {
    setNewLzReferral(
      await getNewReferralAddress({ xStorageClient, provider, account })
    )
  }

  const checkError = async () => {
    if (!token) return
    const error = await checkDepositError({
      provider,
      stakeTokenAddress: token.address,
      amount: amount || '0',
      contractABI,
      contractAddress
    })
    if (error) {
      setDepositErr(error)
    } else {
      setDepositErr(false)
    }
  }

  const enableDeposit = () => {
    const newTokens = [...data.tokens]
    const index = newTokens.findIndex((t: any) => t.address === token.address)
    newTokens[index] = {
      ...token,
      enableDeposit: 1
    }
    setToken(newTokens[index])
    setData({
      ...data,
      tokens: newTokens
    })
  }

  if (!token) {
    return <div />
  }

  const updateBalanceAndStake = (amount: any) => {
    const newTokens = [...data.tokens]
    const index = newTokens.findIndex((t: any) => t.address === token.address)
    newTokens[index] = {
      ...token,
      balance: sub(token.balance, numberToWei(amount))
    }
    setData({
      ...data,
      tokens: newTokens,
      stake: add(data.stake, numberToWei(amount))
    })
  }

  return (
    <Modal visible={visible} setVisible={setVisible}>
      <div className='deposit-modal'>
        <div className='bank-block'>{getText('bank')}</div>
        <div>
          <ArrowUp />
        </div>
        <div className='content-block'>
          <div className='amount-block'>
            <span>{getText('amount')}:</span>
            <span>
              {getText('balance')}: {weiToNumber(token.balance)}
            </span>
          </div>
          <div className='input-block'>
            <input
              className='amount-input'
              type='text'
              value={amount}
              onChange={(e) => {
                if (
                  !isNaN(Number(e.target.value)) &&
                  (token.accAllowance !== '0' || token.enableDeposit)
                ) {
                  setAmount(e.target.value)
                }
              }}
            />
            <button
              disabled={token.accAllowance === '0' && !depositErr}
              className={
                token.accAllowance === '0' && !depositErr
                  ? 'btn-max disabled'
                  : 'btn-max'
              }
              onClick={() => setAmount(weiToExactNumber(token.balance))}
            >
              {getText('max')}
            </button>
            <div className='token-info'>
              {token && <img src={token.logo} alt={token.symbol} />}
              <span>{token.symbol}</span>
            </div>
          </div>
        </div>
        <span className='info'>{getText('depositWarning')}</span>
        <div className='buttons'>
          {token.accAllowance === '0' && !depositErr ? (
            <Button
              onClick={async () => {
                showLoading(true)
                setSending(true)
                const approved = await approveToken({
                  provider,
                  tokenAddress: token.address,
                  contractABI,
                  contractAddress
                })
                if (approved) {
                  enableDeposit()
                }
                setSending(false)
                showLoading(false)
              }}
              style={{ width: '100%' }}
              disabled={
                sending || token.accAllowance !== '0' || token.enableDeposit
              }
            >
              {getText('enable')} {token.symbol}
            </Button>
          ) : (
            ''
          )}
          <Button
            onClick={async () => {
              showLoading(true)
              setSending(true)
              const success = await deposit({
                provider,
                stakeTokenAddress: token.address,
                amount,
                contractABI,
                newLzReferral,
                contractAddress
              })
              if (success) {
                updateBalanceAndStake(amount)
                setVisible(false)
              }
              setSending(false)
              showLoading(false)
            }}
            style={{ width: '100%' }}
            disabled={
              sending ||
              (token.accAllowance === '0' && !token.enableDeposit) ||
              depositErr ||
              Number(amount) === 0
            }
          >
            {depositErr || getText('deposit')}
          </Button>
        </div>
        {token.accAllowance === '0' && (
          <div className='step-deposit'>
            <span
              className={`step ${
                token.enableDeposit ? 'step-not-active' : 'step-active'
              }`}
            >
              1
            </span>
            <span
              className={`step-line ${token.enableDeposit ? 'right' : 'left'}`}
            />
            <span
              className={`step ${
                token.enableDeposit ? 'step-active' : 'step-not-active'
              }`}
            >
              2
            </span>
          </div>
        )}
      </div>
    </Modal>
  )
}
