import React, {
  useCallback,
  useImperativeHandle,
  useRef,
  useState
} from 'react'
import Button from '../Button'
import Modal from '../Modal'
import './style.scss'
export default React.forwardRef(({ message }: { message?: string }, ref) => {
  const [visible, setVisible] = useState(false)
  const dic = useRef<any>({})
  const handleCancel = useCallback(() => {
    dic.current.onCancel && dic.current.onCancel()
    setVisible(false)
    dic.current = {}
  }, [])
  const handleOnConfirm = useCallback(() => {
    dic.current.onConfirm && dic.current.onConfirm()
    setVisible(false)
    dic.current = {}
  }, [])
  const handleVisible = useCallback((visible, onCancel, onConfirm) => {
    setVisible(visible)
    dic.current.onCancel = onCancel
    dic.current.onConfirm = onConfirm
  }, [])
  useImperativeHandle(
    ref,
    () => ({
      handleVisible
    }),
    []
  )

  return (
    <Modal visible={visible} setVisible={setVisible}>
      <div className='warning-dialog'>
        <span className='warning-message'>{message}</span>
        <div className='warning-button'>
          <Button type='white' className='button-cancel' onClick={handleCancel}>
            Cancel
          </Button>
          <Button className='button-confirm' onClick={handleOnConfirm}>
            Confirm
          </Button>
        </div>
      </div>
    </Modal>
  )
})
