import { utils, BigNumber } from 'ethers'

export const shortenAddressString = (address: string) => {
  return (
    address.slice?.(0, 6) +
    '...' +
    address.slice?.(address.length - 4, address.length)
  )
}

export const weiToNumber = (wei: any, decimal: number = 18) => {
  if (!wei || !Number(wei)) return '0'
  wei = wei.toString()
  let number = utils.formatUnits(wei, decimal)
  const arr = number.split('.')
  if (arr.length > 0) {
    arr[1] = arr[1].slice(0, 4)
  }
  number = arr.join('.')
  return number.slice(-2) === '.0' ? number.slice(0, -2) : number
}

export const weiToExactNumber = (wei: any, decimal: number = 18) => {
  if (!wei || !Number(wei)) return '0'
  wei = wei.toString()
  return utils.formatUnits(wei, decimal)
}

export const formatFloat = (number: any, decimal: number = 2) => {
  number = number.toString()
  const arr = number.split('.')
  if (arr.length > 1) {
    arr[1] = arr[1].slice(0, decimal)
  }
  return arr.join('.')
}

export const mul = (a: any, b: any) => {
  return BigNumber.from(a).mul(b).toString()
}

export const sub = (a: any, b: any) => {
  return BigNumber.from(a).sub(b).toString()
}

export const div = (a: any, b: any) => {
  return BigNumber.from(a).div(b).toString()
}

export const add = (a: any, b: any) => {
  return BigNumber.from(a).add(b).toString()
}

export const numberToWei = (number: any, decimal: number = 18) => {
  number = number.toString()
  return utils.parseUnits(number, decimal).toString()
}

export function parseSolidityRevertMessage(res: any) {
  if (!res) {
    return
  }
  if (res.startsWith('0x')) {
    res = res.substring(2)
  }
  // look for solidity revert message
  if (res.length < 2 * (4 + 32 + 32)) {
    return
  }
  const SolidityErrorSignature = '08c379a0' // KECCAK("Error(string)")
  if (!res.startsWith(SolidityErrorSignature)) {
    return
  }
  res = res.substring(4 * 2)
  const offset = parseInt(res.substring(0, 32 * 2), 16) * 2
  res = res.substring(32 * 2)
  const size = parseInt(res.substring(0, 32 * 2), 16) * 2
  if (res.length < offset + size) {
    return
  }
  res = res.substring(offset, offset + size)
  return hex2ASCII(res)
}

function hex2ASCII(str1: any) {
  var hex = str1.toString()
  var str = ''
  for (var n = 0; n < hex.length; n += 2) {
    str += String.fromCharCode(parseInt(hex.substr(n, 2), 16))
  }
  return str
}
export function formatAddressBalances<T extends { toString: () => string }>(
  values: T[],
  addresses: string[],
  tokens: string[]
): {
  [address: string]: {
    [tokenAddress: string]: string
  }
} {
  const balances: {
    [address: string]: {
      [tokenAddress: string]: string
    }
  } = {}
  addresses.forEach((addr, addrIdx) => {
    balances[addr] = {}
    tokens.forEach((tokenAddr, tokenIdx) => {
      const balance = values[addrIdx * tokens.length + tokenIdx]
      balances[addr][tokenAddr] = balance.toString()
    })
  })
  return balances
}
// Time-based cache buster
export const getApiCacheBuster = () => {
  return Math.trunc(Date.now() / (1000 * 60))
}
