import { ethers, BigNumber } from 'ethers'
import { getConfig } from '../utils/config'
import {
  div,
  formatFloat,
  numberToWei,
  mul,
  add,
  weiToNumber,
  formatAddressBalances,
  getApiCacheBuster
} from '../utils/helpers'
import { getText } from '../utils/lang'
import _, { capitalize, toLower, uniq } from 'lodash'
import BalanceCheckerAbi from '../assets/abis/BalanceChecker.abi.json'
import MulticallAbi from '../assets/abis/Multicall.abi.json'
import { abi as erc20 } from '../assets/abis/IERC20.json'
import bscPools from '../assets/bsc_pools.json'
import { toast } from 'react-toastify'
const BalanceCheckerAddrees = '0x2352c63A83f9Fd126af8676146721Fa00924d7e4'
const multicallAddress = '0x41263cba59eb80dc200f3e2544eda4ed6a90e76c'
const limitRetry = 8
const calFund = (totalBalance: string, balance: string) => {
  const fund = div(mul(balance, numberToWei(100)), totalBalance)
  return fund
}
export const TOKEN_LEVEL = {
  STAKE: 2,
  RECEIVING: 1,
  REMOVE: 0
}

const SECONDS_IN_MINUTE = 60
const SECONDS_IN_HOUR = 3600
const SECONDS_IN_DAY = 86400
const SECONDS_IN_MONTH = 2592000

type platform = {
  apePoolAddress: string
  apy: string
  depositAmount: string
  depositUsd: string
  fund: string
  icon: string
  label: string
  lpAddress: string
  names: string[]
  value: number
}
type fund = {
  [lpAddress: string]: {
    balance: string
    tokens: {
      [token: string]: string
    }
  }
}
const keyStateByAccount = [
  'harvestable',
  'stake',
  'referralRates',
  'subsidyRate',
  'contribution',
  'contributionPercent',
  'tokens'
]
const rpcUrl = ['https://bsc-dataseed.binance.org/']
export const providerNotSigner = new ethers.providers.JsonRpcProvider(rpcUrl[0])
const getBeefyUrl = () => {
  const cacheBuster = getApiCacheBuster()
  return `https://api.beefy.finance/apy/breakdown?_=${cacheBuster}`
}
/**
 * Load data from localStorage
 * @param key string
 * @returns
 */
const loadData = (key: string) => {
  return JSON.parse(window.localStorage.getItem(key) ?? 'null')
}
/**
 * Save data to localStorage
 * @param key
 * @param fn
 */
const saveData = (key: string, fn = (state: any) => state) => {
  const oldState = loadData(key) || {}
  const newState = fn(oldState)
  window.localStorage.setItem(key, JSON.stringify(newState))
}
export const fetchData = async ({
  account,
  provider,
  setData
}: {
  provider: any
  account: any
  setData: any
}): Promise<any> => {
  if (!provider) {
    provider = providerNotSigner
    account = getConfig('ZERO_ADDRESS')
  }
  // Load caching
  loadCache(account, setData)
  try {
    const stateData = await fetchInitialState(provider)
    const bankTokenBalances = await getBankTokenBalancesByContract(
      provider,
      [stateData.contractAddress],
      stateData.bankTokenBalances
    )
    stateData.bankTokenBalances = bankTokenBalances
    // get account balance in each token
    const tokenAddress = stateData.tokens.map((tk: any) => tk.address)
    const tokenBalance: {
      [address: string]: string
    } =
      account === getConfig('ZERO_ADDRESS')
        ? tokenAddress.map((tk: string) => ({ [tk]: '0' }))
        : await getTokenBalance(provider, [account], tokenAddress)
    const tokenAllowance: BigNumber[] =
      account === getConfig('ZERO_ADDRESS')
        ? tokenAddress.map(() => '0')
        : await getTokenAllowance(
            provider.getSigner(),
            stateData.contractAddress,
            tokenAddress,
            account,
            stateData.contractABI
          )
    // @ts-ignore
    const stakeQueryData = await fetchStakeQueryData(
      provider,
      account,
      stateData
    )
    const moreQueryConfig = stakeQueryData[1]
    const totalEarn = await getBalance({
      provider,
      account: stateData.contractAddress,
      contractABI: stateData.contractABI,
      // @ts-ignore
      tokenAddress: moreQueryConfig.earnToken
    })

    // @ts-ignore
    const harvestable =
      // @ts-ignore
      Number(stakeQueryData[0].totalValue) === 0
        ? 0
        : div(
            // @ts-ignore
            mul(stakeQueryData[0].value, totalEarn),
            // @ts-ignore
            stakeQueryData[0].totalValue
          )

    const tokens = stateData.tokens
    for (const i in tokens) {
      tokens[i].balance = tokenBalance[tokens[i].address]
        ? BigNumber.from(tokenBalance[tokens[i].address]).toString()
        : '0'
      tokens[i].accAllowance = tokenAllowance[i]
        ? tokenAllowance[i].toString()
        : '0'
    }
    const state = {
      // @ts-ignore
      ...stakeQueryData[0],
      ...stateData,
      ...moreQueryConfig,
      totalEarn: totalEarn.toString(),
      harvestable,
      tokens,
      lockedSeconds: stakeQueryData[2]
    }
    setData(state, account)
    storeCache(state, account)
  } catch (error) {}
}
export const fetchStakeQueryData = async (
  provider: any,
  account: any,
  stateData: any,
  retry = 0
): Promise<any> => {
  try {
    const promises = [
      // get stake query
      fetchStakeQuery({
        provider,
        account,
        contractABI: stateData.contractABI,
        contractAddress: stateData.contractAddress
      }),
      getMoreQueryConfig({
        provider,
        contractABI: stateData.contractABI,
        contractAddress: stateData.contractAddress
      }),
      getLockedSeconds({
        provider,
        address: account,
        bankAddress: stateData.contractAddress
      })
    ]
    const stakeQueryData = await Promise.all([...promises])
    saveData('stakeQueryData', (oldState) => {
      oldState[account] = stakeQueryData
      return oldState
    })
    return stakeQueryData
  } catch (error) {
    if (retry > limitRetry) {
      toast.error('State data cannot be load, please try again later.')
      const oldState = loadData('stakeQueryData')
      if (oldState) return oldState[account]
      return Promise.reject(error)
    }
    return await fetchStakeQueryData(provider, account, stateData, ++retry)
  }
}
export const loadCache = (account: any, setData?: any): any => {
  try {
    if (!account) return {}
    // Load caching
    const oldState = JSON.parse(window.localStorage.getItem('state') ?? 'null')
    if (oldState) {
      const tmp = {}
      for (const key in oldState) {
        if (Object.prototype.hasOwnProperty.call(oldState, key)) {
          const data = oldState[key]
          if (keyStateByAccount.includes(key)) {
            tmp[key] = data[account]
          } else {
            tmp[key] = data
          }
        }
      }
      // Apply caching
      setData && setData(tmp, account)
      return tmp
    }
    return {}
  } catch (error) {
    return {}
    console.error('load cache error', error)
  }
}
export const storeCache = (state: any, account: any) => {
  const oldState = JSON.parse(window.localStorage.getItem('state') ?? 'null')
  const caching = oldState || {}
  for (const key in state) {
    if (Object.prototype.hasOwnProperty.call(state, key)) {
      const data = state[key]
      if (keyStateByAccount.includes(key)) {
        caching[key] = caching[key]
          ? { ...caching[key], [account]: state[key] }
          : { [account]: state[key] }
      } else {
        caching[key] = data
      }
    }
  }
  // Caching state
  window.localStorage.setItem('state', JSON.stringify(caching))
}
export const getTokenAllowance = async (
  provider: any,
  contractAddress: string,
  tokens: string[],
  account: string,
  contractAbi: any,
  retry = 0
): Promise<any> => {
  try {
    const callData = tokens.map((tk) => {
      return {
        address: tk,
        name: 'allowance',
        params: [account, contractAddress]
      }
    })
    const res = await multicall(contractAbi, callData, provider)

    const ret = res.map((allowance: any) => allowance[0])
    saveData('tokenAllowance', (oldState) => {
      oldState[account] = ret
      return oldState
    })
    return ret
  } catch (error) {
    if (retry > limitRetry) {
      toast.error('Token allowance cannot be load, please try again later.')
      const oldState = loadData('tokenAllowance')
      if (oldState) return oldState[account]
      return Promise.reject(error)
    }
    return await getTokenAllowance(
      provider,
      contractAddress,
      tokens,
      account,
      contractAbi,
      ++retry
    )
  }
}
export const multicall = async <T = any>(
  abi: any[],
  calls: {
    address: string // Address of the contract
    name: string // Function name on the contract (example: balanceOf)
    params?: any[] // Function params
  }[],
  provider: any
): Promise<T> => {
  try {
    const multi = new ethers.Contract(multicallAddress, MulticallAbi, provider)
    const itf = new ethers.utils.Interface(abi)

    const calldata = calls.map((call) => [
      call.address.toLowerCase(),
      itf.encodeFunctionData(call.name, call.params)
    ])
    const { returnData } = await multi.aggregate(calldata)

    const res = returnData.map((call: any, i: any) =>
      itf.decodeFunctionResult(calls[i].name, call)
    )
    return res
  } catch (error) {
    console.log('errors', error)
    throw new Error(error)
  }
}

export const getTokenBalance = async (
  provider: any,
  accounts: string[],
  tokens: string[],
  reTry = 0
): Promise<{ [key: string]: string }> => {
  try {
    const balanceContract = new ethers.Contract(
      BalanceCheckerAddrees,
      BalanceCheckerAbi,
      provider
    )
    const tokenAddress = tokens
    const balances = await balanceContract.balances(accounts, tokenAddress)
    const res = formatAddressBalances(balances, accounts, tokenAddress)
    saveData('tokenBalance', (oldState) => {
      oldState[accounts[0]] = res[accounts[0]]
      return oldState
    })
    return res[accounts[0]]
  } catch (error) {
    if (reTry > limitRetry) {
      toast.error('Token balance cannot be load, please try again later.')
      const oldState = loadData('tokenBalance')
      if (oldState) return oldState[accounts[0]]
      return Promise.reject(error)
    }
    return await getTokenBalance(provider, accounts, tokens, ++reTry)
  }
}
export const getBankTokenBalancesByContract = async (
  provider: any,
  accounts: string[],
  bankTokenBalances: {
    [token: string]: string
  },
  reTry = 0
): Promise<{ [key: string]: string }> => {
  try {
    const balanceContract = new ethers.Contract(
      BalanceCheckerAddrees,
      BalanceCheckerAbi,
      provider
    )
    const tokenAddress = Object.keys(bankTokenBalances)
    const balances = await balanceContract.balances(accounts, tokenAddress)
    const res = formatAddressBalances(balances, accounts, tokenAddress)
    return res[accounts[0]]
  } catch (error) {
    if (reTry > limitRetry) {
      return bankTokenBalances
    }
    return await getBankTokenBalancesByContract(
      provider,
      accounts,
      bankTokenBalances,
      ++reTry
    )
  }
}
export const fetchInitialState = async (
  provider: any,
  retry = 0
): Promise<any> => {
  try {
    const keys = ['tokens', 'harvested']
    const keysQuery = keys.join(',')
    const parts = await Promise.all([
      fetch(`${getConfig('SFARM_API_ENDPOINT')}/sfarm/queryConfig`)
        .then((m) => m.json())
        .then((m) => m.data),
      fetch(`${getConfig('SFARM_API_ENDPOINT')}/sfarm/state/${keysQuery}`)
        .then((m) => m.json())
        .then((m) => m.data),
      fetch(getConfig('TOKEN_INFO_ENDPOINT')).then((m) => m.json()),
      fetch(`${getConfig('SFARM_API_ENDPOINT')}/sfarm/balance`)
        .then((m) => m.json())
        .then((m) => m.data)
        .then((m) => _.pickBy(m, (v: any) => v !== '0')),
      fetch(`${getConfig('SFARM_API_ENDPOINT')}/sfarm/liquidity`)
        .then((m) => m.json())
        .then((m) => m.data),
      initFarmData(),
      fetch(`${getConfig('SFARM_API_ENDPOINT')}/sfarm/funds`)
        .then((m) => m.json())
        .then((m) => m.data),
      fetch(getBeefyUrl()).then((m) => m.json())
    ])

    const fullFundPlatforms = await getFullFundPlatforms(
      parts[5],
      parts[1]['sfarm-tokens'],
      parts[3],
      parts[6],
      parts[7],
      provider
    )
    const data = parts
      .slice(0, 2)
      .reduce((data, part) => Object.assign(data, part), {})

    const tokensInfo = parts[2]
    let tokens = mapToArray(data['sfarm-tokens'])
      .filter((token: any) => {
        return token.mask === TOKEN_LEVEL.STAKE
      })
      .map((token: any) => {
        const tokenInfo = tokensInfo.find(
          (e: any) => e?.address?.toLowerCase() === token.address.toLowerCase()
        )
        return { ...tokenInfo, ...token }
      })
    tokens = await manualGetInfoToken(tokens, provider)
    const result = {
      harvested: data['sfarm-harvested'],
      contractAddress: data.contractAddress,
      contractABI: data.contractABI,
      tokens: tokens,
      bankTokens: data['sfarm-tokens'],
      bankTokenBalances: parts[3],
      bankLiquidity: parts[4],
      lzToken: tokensInfo.find(
        (e: any) => e?.address?.toLowerCase() === data.earnToken.toLowerCase()
      ),
      tokensInfo,
      ...fullFundPlatforms
    }
    saveData('fetchInitialState', () => result)
    return result
  } catch (error) {
    if (retry > limitRetry) {
      toast.error('Initial data cannot be load, please try again later.')
      const oldState = loadData('fetchInitialState')
      if (oldState) return oldState
      return Promise.reject(error)
    }
    console.info('retry fetch initial data')
    return await fetchInitialState(provider, ++retry)
  }
}
/**
 * Manual fetch info of token not in tokensInfo
 * @param tokens
 * @param provider
 * @returns
 */
const manualGetInfoToken = async (tokens: any[], provider: any) => {
  const tokenMissInfo = tokens
    .filter((tk) => !tk.symbol)
    .reduce((result, next) => {
      result[next.address] = next
      return result
    }, {})
  const tokenMissInfoAddress = Object.values(tokenMissInfo)
  if (tokenMissInfoAddress.length) {
    const newTokenInfo = await Promise.all(
      tokenMissInfoAddress.map((tk: any) => getTokenInfo(tk.address, provider))
    )
    newTokenInfo.forEach(({ address, symbol, name }) => {
      tokenMissInfo[address] = {
        ...tokenMissInfo[address],
        symbol: symbol,
        name,
        logo: `${getConfig('FARMY_API_ENDPOINT')}/token/${symbol}.webp`
      }
    })
    tokens = tokens
      .filter((tk) => tk.symbol)
      .concat(Object.values(tokenMissInfo))
  }
  return tokens
}
const getFullFundPlatforms = async (
  initFarmData: any,
  farmTokens: { [token: string]: number }[],
  balances: {
    [token: string]: any
  },
  funds: fund,
  aprData: any,
  provider: any
) => {
  const balanceUnallocated: { [token: string]: string } = Object.keys(
    farmTokens
  ).reduce((result, next) => {
    if (farmTokens[next] === 2) {
      result[next] = balances[next] || '0'
      return result
    }
    return result
  }, {})

  let totalBalance: string = Object.values(balanceUnallocated).reduce(
    (total: string, balance: string) => {
      return add(total, balance)
    },
    '0'
  )

  const { fundPlatforms } = initFarmData
  let platforms: {
    apePoolAddress: string
    apy: string
    depositAmount: string
    depositUsd: string
    fund: string
    icon: string
    label: string
    lpAddress: string
    names: string[]
    value: number
  }[] = []
  const tokenList: any[] = uniq(
    Object.keys(balances).concat(Object.keys(farmTokens))
  )
  // Fetch token info
  const tokensInfor = await Promise.all(
    tokenList.map((tokenAddress) => getTokenInfo(tokenAddress, provider))
  ).then((data) =>
    data.reduce((result: any, next) => {
      result[next.address] = next
      return result
    }, {})
  )
  for (const lpAddress in funds) {
    // Map quantity of fund
    const balance = funds[lpAddress].balance

    const tokens = funds[lpAddress].tokens
    // Cal value of fund
    const value = Object.values(tokens).reduce((result, next) => {
      result = add(result, next)
      return result
    }, '0')
    totalBalance = add(totalBalance, value)
    // Find name, label, PoolAddress of Fund
    let names: any = []
    let apePoolAddress = ''
    const platformFarmarmy =
      fundPlatforms.find(({ extra }: any) => {
        const { lpAddress: lpAddressExtra, transactionToken } = extra
        return lpAddressExtra === lpAddress || transactionToken === lpAddress
      }) || {}
    names = platformFarmarmy.name ? platformFarmarmy.name.split('-') : []
    apePoolAddress = platformFarmarmy.extra
      ? platformFarmarmy.extra.transactionAddress
      : ''
    // Find id of farm from https://www.beefy.finance/
    const id = bscPools[lpAddress]
    platforms.push({
      apePoolAddress: apePoolAddress,
      lpAddress: lpAddress,
      depositUsd: '',
      apy: id && aprData[id] ? numberToWei(aprData[id].totalApy * 100) : '',
      names: names,
      label: capitalize(platformFarmarmy.platform) || '',
      icon: platformFarmarmy.icon
        ? `https://farm.army/assets/platforms/${toLower(
            platformFarmarmy.platform
          )}.png`
        : '',
      value: parseFloat(weiToNumber(value)),
      depositAmount: balance,
      fund: ''
    })
  }

  // Mapping fund
  platforms = platforms.map((el) => {
    const fund = calFund(totalBalance, numberToWei(el.value))
    return { ...el, fund: fund.toString() }
  })
  // Mapping names, label
  platforms = platforms.map((el) => ({
    ...el,
    names:
      el.names ||
      Object.keys(funds[el.lpAddress].tokens).map(
        (tk) => tokensInfor[tk].symbol
      )
  }))
  initFarmData.fundPlatforms = _.orderBy(platforms, ['value'], ['desc'])
  // Caculate token asset balance with apy
  const tokenApy = calTokenWithApy(
    platforms,
    funds,
    balanceUnallocated,
    tokensInfor
  )
  initFarmData.tokensWithAPY = Object.values(tokenApy.tokensWithAPY)
  initFarmData.bankApy = tokenApy.bankApy

  initFarmData.fundPlatformUnallocated = getFundUnallocated(
    balanceUnallocated,
    tokensInfor,
    totalBalance
  )
  return initFarmData
}
// {
//   name,
//   totalDeposit: p.depositUsd,
//   depositWithAPY: mul(p.depositUsd, p.apy)
// }
const calTokenWithApy = (
  platforms: platform[],
  funds: fund,
  balanceUnallocated: {
    [token: string]: string
  },
  tokensInfor: {
    [token: string]: {
      symbol: string
      address: string
    }
  }
) => {
  const tokensWithAPY: {
    [name: string]: {
      name: string
      totalDeposit: string
      depositWithAPY: string
      unallocated: string
    }
  } = {}
  let bankApy = '0'
  for (const lpAddress in funds) {
    if (Object.prototype.hasOwnProperty.call(funds, lpAddress)) {
      const { tokens } = funds[lpAddress]
      const { apy: farmAPY, fund } = platforms.find(
        (farm) => farm.lpAddress === lpAddress
      ) || { apy: '0', fund: '0' }
      bankApy = add(bankApy, div(mul(farmAPY, fund), numberToWei(100)))
      for (const tokenAddress in tokens) {
        if (Object.prototype.hasOwnProperty.call(tokens, tokenAddress)) {
          const depositUsd = tokens[tokenAddress]
          const name = tokensInfor[tokenAddress].symbol
          if (tokensWithAPY[name]) {
            tokensWithAPY[name].totalDeposit = add(
              tokensWithAPY[name].totalDeposit,
              depositUsd
            )
            tokensWithAPY[name].depositWithAPY = add(
              tokensWithAPY[name].depositWithAPY,
              mul(depositUsd, farmAPY)
            )
          } else {
            tokensWithAPY[name] = {
              name: name,
              totalDeposit: depositUsd,
              depositWithAPY: mul(depositUsd, farmAPY),
              unallocated: balanceUnallocated[tokenAddress]
            }
          }
        }
      }
    }
  }
  for (const name in tokensWithAPY) {
    if (Object.prototype.hasOwnProperty.call(tokensWithAPY, name)) {
      const { totalDeposit, depositWithAPY, unallocated } = tokensWithAPY[name]
      const finalDepositWithAPY = div(
        depositWithAPY,
        add(unallocated, totalDeposit)
      )
      tokensWithAPY[name].depositWithAPY = finalDepositWithAPY
    }
  }
  return { tokensWithAPY, bankApy }
}
const getFundUnallocated = (
  balanceUnallocated: any,
  tokensInfor: {
    [token: string]: {
      symbol: string
    }
  },
  totalBalance: string
) => {
  const result: {
    fund: string
    value: string
    names: string[]
    balance: {
      [token: string]: string
    }
  } = {
    fund: '0',
    value: '0',
    names: [],
    balance: {}
  }
  for (const token in balanceUnallocated) {
    if (Object.prototype.hasOwnProperty.call(balanceUnallocated, token)) {
      const balance = balanceUnallocated[token]
      // eslint-disable-next-line eqeqeq
      if (balance == 0) {
        continue
      }
      result.value = result.value
        ? add(result.value, balance)
        : add('0', balance)
      result.names.push(
        tokensInfor[token].symbol +
          ',' +
          formatFloat(weiToNumber(balance), 2) +
          ',' +
          token
      )
    }
  }
  result.names = _.orderBy(
    result.names,
    (name) => {
      const tmp = name.split(',')
      return tmp[2]
    },
    ['desc']
  )
  result.fund = calFund(totalBalance, result.value)
  result.value = weiToNumber(result.value)
  return result
}

const getTokenInfo = async (tokenAddress: string, provider: any) => {
  const contract = new ethers.Contract(tokenAddress, erc20, provider)
  const symbol = await contract.functions.symbol()
  const name = await contract.functions.name()
  return {
    symbol: symbol[0],
    address: tokenAddress,
    name: name[0]
  }
}
export const fetchStakeQuery = async ({
  provider,
  account,
  contractABI,
  contractAddress
}: {
  provider: any
  account: any
  contractABI: any
  contractAddress: string
}) => {
  try {
    const SfarmContract = new ethers.Contract(
      contractAddress,
      contractABI,
      provider
    )
    const data = await SfarmContract.functions.query(account)
    const contribution = data.contribution.lt('0')
      ? ethers.BigNumber.from(0)
      : data.contribution
    return {
      stake: data.stake.toString(),
      totalStake: data.totalStake.toString(),
      value: contribution.toString(),
      totalValue: data.totalContribution.toString(),
      contribution: secondsToContribution(
        div(contribution, data.stake.isZero() ? data.totalStake : data.stake)
      ),
      contributionPercent: div(
        mul(contribution, numberToWei(100)),
        data.totalContribution
      ),
      totalContribution: secondsToContribution(
        div(data.totalContribution, data.totalStake)
      )
    }
  } catch (e) {
    console.log(e)
    return {
      stake: '',
      totalStake: '',
      value: '',
      totalValue: '',
      contribution: '',
      totalContribution: ''
    }
  }
}

export const getBalance = async ({
  provider,
  account,
  contractABI,
  tokenAddress,
  retry = 0
}: {
  provider: any
  account: any
  contractABI: any
  tokenAddress: string
  retry?: number
}): Promise<any> => {
  try {
    const SfarmContract = new ethers.Contract(
      tokenAddress,
      contractABI,
      provider
    )
    const data = await SfarmContract.functions.balanceOf(account)
    return data[0] ? data[0] : 0
  } catch (e) {
    if (retry > limitRetry) {
      toast.error('Balance cannot be load, please try again later.')
      return 0
    }
    return await getBalance({
      provider,
      account,
      contractABI,
      tokenAddress,
      retry: ++retry
    })
  }
}

export const getAllowance = async ({
  provider,
  account,
  contractABI,
  contractAddress,
  tokenAddress
}: {
  provider: any
  account: any
  contractABI: any
  contractAddress: string
  tokenAddress: string
}) => {
  try {
    const signer = provider.getSigner()
    const SfarmContract = new ethers.Contract(tokenAddress, contractABI, signer)
    const data = await SfarmContract.functions.allowance(
      account,
      contractAddress
    )
    return data[0] ? weiToNumber(data[0]) : '0'
  } catch (e) {
    console.log(e)
    return 0
  }
}

export const getMoreQueryConfig = async ({
  provider,
  contractABI,
  contractAddress,
  retry = 0
}: {
  provider: any
  contractABI: any
  contractAddress: string
  retry?: number
}): Promise<any> => {
  try {
    const SfarmContract = new ethers.Contract(
      contractAddress,
      contractABI,
      provider
    )
    const result = await SfarmContract.functions.queryConfig()
    const ret = {
      referralRates: result.refRates_.map((a: any) =>
        ethers.BigNumber.from(a).toString()
      ),
      earnToken: result.earnToken_,
      subsidyRate: result.subsidyRate_.toString()
    }
    saveData('moreQueryConfig', () => ret)
    return ret
  } catch (error) {
    if (retry > limitRetry) {
      toast.error('More query config cannot be load, please try again later.')
      const oldState = loadData('moreQueryConfig')
      if (oldState) return oldState
      return Promise.reject(error)
    }
    return await getMoreQueryConfig({
      provider,
      contractABI,
      contractAddress,
      retry: ++retry
    })
  }
}

export const secondsToContribution = (timeStamp: string) => {
  if (Number(timeStamp) < SECONDS_IN_HOUR) {
    return (
      formatFloat(
        weiToNumber(div(numberToWei(timeStamp), SECONDS_IN_MINUTE)),
        2
      ) + ` ${getText('minutes')}`
    )
  } else if (Number(timeStamp) < SECONDS_IN_DAY) {
    return (
      formatFloat(
        weiToNumber(div(numberToWei(timeStamp), SECONDS_IN_HOUR)),
        2
      ) + ` ${getText('hours')}`
    )
  } else if (Number(timeStamp) < SECONDS_IN_MONTH) {
    return (
      formatFloat(weiToNumber(div(numberToWei(timeStamp), SECONDS_IN_DAY)), 2) +
      ` ${getText('days')}`
    )
  } else {
    return (
      formatFloat(
        weiToNumber(div(numberToWei(timeStamp), SECONDS_IN_MONTH)),
        2
      ) + ` ${getText('months')}`
    )
  }
}

