export const getEarnHistories = async (lastestBlock: number, limit = 30) => {
  try {
    const res = await fetch(
      `${getConfig('SFARM_API_ENDPOINT')}/sfarm/earnHistory?limit=${limit}`
    ).then((m) => m.json())
    const resData = res.data
    resData.unshift({
      block: lastestBlock,
      balance: resData[0].balance
    })
    const labels = []
    const data = []
    for (let i = resData.length - 1; i >= 0; i--) {
      data.push({
        y: Number(weiToNumber(resData[i].balance)),
        x: new Date().getTime() - (lastestBlock - resData[i].block) * 3000
      })
      labels.push(
        new Date().getTime() - (lastestBlock - resData[i].block) * 3000
      )
    }

    return { labels, data, earnHistories: resData }
  } catch (e) {
    console.log(e)
    return null
  }
}

export const getLockedSeconds = async ({
  provider,
  bankAddress,
  address,
  retry = 0
}: {
  provider: any
  bankAddress: string
  address: string
  retry?: number
}): Promise<any> => {
  try {
    const slot = 12 // stakes
    const MAX_T = 0xffffffffffffffff
    const p = slot.toString(16).padStart(64, '0')
    const k = '0'.repeat(24) + address.substr(2)
    const position = ethers.utils.keccak256('0x' + k + p)
    // const stake = await web3.eth.getStorageAt(bankAddress, position, 'pending')
    const stake = await provider.getStorageAt(bankAddress, position)
    // const s = ethers.BigNumber.from('0x'+stake.substr(18))
    const t = parseInt(stake.substr(2, 16), 16)
    if (t >= MAX_T) {
      // no stake
      return 0
    }
    const timestamp = Date.now() / 1000
    if (timestamp >= t) {
      // no lock
      return 0
    }
    return t - timestamp
  } catch (error) {
    if (retry > limitRetry) {
      toast.error('Locked cannot be load, please try again later.')
      return Promise.reject(error)
    }
    return await getLockedSeconds({
      provider,
      bankAddress,
      address,
      retry: ++retry
    })
  }
}

