import { BigNumber } from 'ethers'
import { getConfig } from '../utils/config'
import { numberToWei, weiToNumber } from '../utils/helpers'

const SECONDS_IN_YEAR = 365 * 24 * 3600 * 1000
export const fetchLastHarvest = async (earnHistories: any[]) => {
  try {
    const t = earnHistories.find((earn: any) => earn.amount > 0)
    if (t) return t
    const lastHarvest = await fetch(
      `${getConfig('SFARM_API_ENDPOINT')}/sfarm/lastHarvest`
    ).then((m) => m.json())
    return lastHarvest.data
  } catch (error) {
    return null
  }
}
export const getEstYield = async (
  stake: string,
  bankApy: string,
  earnHistories: any,
  lastestBlock: number
) => {
  const lastHarvest = await fetchLastHarvest(earnHistories)
  if (!lastHarvest)
    return {
      estAccount: null,
      lastTimeHarvested: null
    }
  const duration = (lastestBlock - lastHarvest.block) * 3 * 1000
  const lastTimeHarvested = new Date().getTime() - duration
  const estAccount = weiToNumber(
    BigNumber.from(stake)
      .mul(bankApy)
      .div(numberToWei(100))
      .mul(duration)
      .div(SECONDS_IN_YEAR)
  )
  return {
    estAccount,
    lastTimeHarvested
  }
}
