import React, { useEffect } from 'react'
import './style.scss'
import { ExitIcon } from '../Icons'

export default ({
  setVisible,
  visible,
  children
}: {
  setVisible: any
  visible: any
  children: any
}) => {
  useEffect(() => {
    window.addEventListener('keyup', (e: any) => {
      if (e.which === 27) {
        setVisible(false)
      }
    })
  }, [])

  return (
    <div className={`sfarm-modal  ${visible ? 'show' : ''}`}>
      <div className='overlay' onClick={() => setVisible(false)} />
      <div className='modal'>
        <div className='btn-close-wrap'>
          <span className='btn-close' onClick={() => setVisible(false)}>
            <ExitIcon />
          </span>
        </div>
        {children}
      </div>
    </div>
  )
}
