import React, { useEffect, useState, useCallback } from 'react'
import './style.scss'
import Button from '../Button'
import { Input } from '../Input'
import { SearchIcon, SortDown, SortUp, SortDisabled } from '../Icons'
import ModalDeposit from '../ModalDeposit'
import ModalWithdraw from '../ModalWithdraw'
import { formatFloat, weiToNumber } from '../../utils/helpers'
import { BigNumber } from 'ethers'
import { getText } from '../../utils/lang'
import { fetchFarmLiquidity } from '../../service/get-current-state'
import { orderBy } from 'lodash'

const SORT_BALANCE_LOW_TO_HIGH = '1'
const SORT_BALANCE_HIGH_LOW_TO = '2'
const TYPE_SORT = {
  ASC: 'asc',
  DESC: 'desc'
}
export const Logo = ({ ...props }) => {
  const [err, setErr] = useState(false)
  if (err) return <div className='token-logo-error' />

  return (
    <img
      {...props}
      onError={() => {
        setErr(true)
      }}
    />
  )
}
export default ({
  useData,
  setData,
  showLoading,
  useWeb3React,
  xStorageClient
}: {
  useData: any
  useWeb3React: any
  showLoading: any
  setData: any
  xStorageClient: any
}) => {
  const { tokens, lockedSeconds, tokensWithAPY, stake } = useData()
  const [visibleDeposit, setVisibleDeposit] = useState<any>(false)
  const [tokensDisplay, setTokensDisplay] = useState<any>(false)
  const [visibleWithdraw, setVisibleWithdraw] = useState<any>(false)
  const [tokenChoosing, setTokenChoosing] = useState<any>()
  const [searchString, setSearchString] = useState<string>('')
  const [sortType] = useState<string>('')
  const [sort, setSort] = useState<any>({
    balance: 'desc'
  })
  useEffect(() => {
    if (tokens) {
      setTokensDisplay(tokens.filter(searchHandle).sort(sortHandle))
    } else {
      setTokensDisplay(false)
    }
  }, [searchString, sortType, tokens])

  const refreshFarmLquidity = useCallback(() => {
    const data = useData()
    fetchFarmLiquidity().then((bankLiquidity: any) => {
      setData({
        ...data,
        bankLiquidity
      })
    })
  }, [setData, useData])
  // useEffect(() => {
  //   setTokensDisplay(tokens)
  // }, [tokens])

  const searchHandle = (e: any) => {
    return (
      e.name?.toLowerCase().includes(searchString.toLowerCase()) ||
      e.symbol?.toLowerCase().includes(searchString.toLowerCase())
    )
  }

  const sortHandle = (a: any, b: any) => {
    if (sortType === SORT_BALANCE_LOW_TO_HIGH) {
      return parseFloat(a.balance) - parseFloat(b.balance)
    } else if (sortType === SORT_BALANCE_HIGH_LOW_TO) {
      return parseFloat(b.balance) - parseFloat(a.balance)
    }
    return null
  }
  const handleSortName = useCallback(() => {
    setSort({
      name: sort.name === TYPE_SORT.ASC ? TYPE_SORT.DESC : TYPE_SORT.ASC
    })
  }, [sort])
  const handleSortBalance = useCallback(() => {
    setSort({
      balance: sort.balance === TYPE_SORT.DESC ? TYPE_SORT.ASC : TYPE_SORT.DESC
    })
  }, [sort])
  const handleSortApy = useCallback(() => {
    setSort({
      apy: sort.apy === TYPE_SORT.DESC ? TYPE_SORT.ASC : TYPE_SORT.DESC
    })
  }, [sort])
  useEffect(() => {
    if (tokens && sort.name) {
      setTokensDisplay(
        orderBy(
          tokens.filter(searchHandle),
          'name',
          sort.name === 'asc' ? 'asc' : 'desc'
        )
      )
    }
  }, [sort.name, tokens])
  useEffect(() => {
    if (tokens && sort.balance) {
      setTokensDisplay(
        orderBy(
          tokens.filter(searchHandle),
          'balance',
          sort.balance === 'asc' ? 'asc' : 'desc'
        )
      )
    }
  }, [sort.balance, tokens])
  useEffect(() => {
    if (tokens && sort.apy) {
      setTokensDisplay(
        orderBy(
          tokens.filter(searchHandle),
          (tk) => {
            const depositApy =
              tokensWithAPY.find((t: any) => t.name === tk.symbol)
                ?.depositWithAPY || 0
            return (
              parseFloat(weiToNumber(depositApy)) ||
              (sort.apy === 'asc'
                ? Number.MAX_SAFE_INTEGER
                : Number.MIN_SAFE_INTEGER)
            )
          },
          sort.apy === 'asc' ? 'asc' : 'desc'
        )
      )
    }
  }, [sort.apy, tokensWithAPY, tokens])
  const renderIcon = useCallback((type) => {
    if (!type) return <SortDisabled />
    return type === 'asc' ? <SortUp /> : <SortDown />
  }, [])
  return (
    <div className='token-assets'>
      <div className='head'>
        <span className='text-gradient hidden-on-phone font-size-18 font-weight-5'>
          {getText('assets')}
        </span>
        <div className='filter'>
          <Input
            placeholder={getText('search_farm')}
            suffix={<SearchIcon />}
            onChange={(e: any) => setSearchString(e.target.value)}
          />
        </div>
      </div>
      <div className='assets-content'>
        <div className='tables'>
          <div className='table-box'>
            <table className='table'>
              <thead>
                <tr>
                  <th>
                    <div onClick={handleSortName} className='table-header'>
                      <span>{getText('asset')}</span>
                      {renderIcon(sort.name)}
                    </div>
                  </th>
                  <th>
                    <div onClick={handleSortBalance} className='table-header'>
                      <span>{getText('balance')}</span>
                      {renderIcon(sort.balance)}
                    </div>
                  </th>
                  {tokensWithAPY && tokensWithAPY.length > 0 && (
                    <th>
                      <div onClick={handleSortApy} className='table-header'>
                        <span>{getText('apy')}</span>
                        {renderIcon(sort.apy)}
                      </div>
                    </th>
                  )}
                  <th />
                </tr>
              </thead>
              <tbody>
                {tokensDisplay
                  ? tokensDisplay.map((token: any, key: number) => {
                      const result: {
                        totalDeposit?: string
                        depositWithAPY?: string
                      } = tokensWithAPY
                        ? tokensWithAPY.find(
                            (t: any) => t.name === token.symbol
                          )
                        : null
                      const { depositWithAPY: apy } = result || {}
                      return (
                        <tr key={key}>
                          <td>
                            <div>
                              <Logo
                                src={token.logo}
                                className={
                                  token.logo
                                    ? 'token-logo'
                                    : 'token-logo-transparent'
                                }
                              />
                              <span className='symbol'>{token.symbol}</span>
                            </div>
                          </td>
                          <td className='balance-td'>
                            <p className='text-default user-balance'>
                              <span className='balance'>
                                {weiToNumber(token.balance) || 0}
                              </span>
                            </p>
                          </td>
                          {tokensWithAPY && tokensWithAPY.length > 0 && (
                            <td>
                              <p className='text-green-1 user-balance'>
                                <span className='balance'>
                                  {apy
                                    ? formatFloat(weiToNumber(apy), 2) + '%'
                                    : ''}
                                </span>
                              </p>
                            </td>
                          )}
                          <td>
                            <div>
                              <Button
                                type='white'
                                size='large'
                                className='font-weight'
                                onClick={() => {
                                  setVisibleDeposit(true)
                                  setTokenChoosing(token)
                                }}
                                disabled={
                                  token.balance == null ||
                                  (token.balance &&
                                    BigNumber.from(token.balance).isZero())
                                }
                              >
                                {getText('deposit')}
                              </Button>
                              <Button
                                type='white'
                                size='large'
                                className='font-weight'
                                onClick={() => {
                                  refreshFarmLquidity()
                                  setVisibleWithdraw(true)
                                  setTokenChoosing(token)
                                }}
                                // disabled={Number(lockedSeconds) > 0}
                                disabled={
                                  lockedSeconds == null ||
                                  lockedSeconds > 0 ||
                                  stake == null ||
                                  (stake && BigNumber.from(stake).isZero())
                                }
                              >
                                {getText('withdraw')}
                              </Button>
                            </div>
                          </td>
                        </tr>
                      )
                    })
                  : [1, 2, 3, 4].map((_, key) => (
                      <tr key={key}>
                        <td>
                          <div>
                            <div className='skeleton-loader__bone token-logo radius-100' />
                            <span className='skeleton-loader__bone flex-1 symbol-transparent'>
                              BUSD
                            </span>
                          </div>
                        </td>
                        <td className='balance-td'>
                          <p className='text-default user-balance'>
                            <div className='skeleton-loader__bone flex-1' />
                          </p>
                        </td>
                        {tokensWithAPY && tokensWithAPY.length > 0 && (
                          <td>
                            <p className='text-green-1 user-balance'>
                              <div className='skeleton-loader__bone flex-1' />
                            </p>
                          </td>
                        )}
                        <td>
                          <div>
                            <Button
                              type='white'
                              size='large'
                              className='font-weight'
                              disabled
                            >
                              {getText('deposit')}
                            </Button>
                            <Button
                              type='white'
                              size='large'
                              className='font-weight'
                              // disabled={Number(lockedSeconds) > 0}
                              disabled
                            >
                              {getText('withdraw')}
                            </Button>
                          </div>
                        </td>
                      </tr>
                    ))}
              </tbody>
            </table>
          </div>
        </div>
        <ModalDeposit
          showLoading={showLoading}
          visible={visibleDeposit}
          setVisible={setVisibleDeposit}
          tokenChoosing={tokenChoosing}
          useWeb3React={useWeb3React}
          useData={useData}
          setData={setData}
          xStorageClient={xStorageClient}
        />
        <ModalWithdraw
          showLoading={showLoading}
          visible={visibleWithdraw}
          setVisible={setVisibleWithdraw}
          tokenChoosing={tokenChoosing}
          useWeb3React={useWeb3React}
          useData={useData}
          setData={setData}
        />
      </div>
    </div>
  )
}
