import React, { useCallback, useEffect, useState } from 'react'
import './style.scss'
import Button from '../Button'
import { ArrowDown } from '../Icons'
import {
  add,
  numberToWei,
  sub,
  weiToExactNumber,
  weiToNumber
} from '../../utils/helpers'
import {
  withdraw,
  checkWithdrawError,
  getWithdrawable
} from '../../service/withdraw'
import Modal from '../Modal'
import { getText } from '../../utils/lang'
import { BigNumber } from 'ethers'
import { toast } from 'react-toastify'

export default ({
  setVisible,
  visible,
  tokenChoosing: token,
  useWeb3React,
  useData,
  setData,
  showLoading
}: {
  setVisible: any
  visible: any
  tokenChoosing: any
  useWeb3React: any
  useData: any
  setData: any
  showLoading: any
}) => {
  const { library: provider, account } = useWeb3React()
  const [amount, setAmount] = useState<any>(0)
  const [withdrawable, setWithdrawable] = useState<any>('')
  const { stake } = useData()
  const data = useData()
  const [sending, setSending] = useState<boolean>(false)
  const [withdrawErr, setWithdrawErr] = useState<any>(false)

  useEffect(() => {
    window.addEventListener('keyup', (e: any) => {
      if (e.which === 27) {
        setVisible(false)
      }
    })
  }, [])

  useEffect(() => {
    if (token && visible) {
      setWithdrawable('...')
      fetchWithdrawable()
      setAmount(0)
    }
  }, [token, visible])

  useEffect(() => {
    setWithdrawErr('')
  }, [visible])

  useEffect(() => {
    checkError()
  }, [amount])

  const fetchWithdrawable = async () => {
    try {
      const w = await getWithdrawable({
        token: token.address,
        data,
        provider
      })
      setWithdrawable(w)
    } catch (error) {
      toast.error('Withdrawable data cannot be load, please try again later.')
    }
  }

  const checkError = async () => {
    if (!token) return
    const error = await checkWithdrawError({
      provider,
      stakeTokenAddress: token.address,
      amount: amount || '0',
      data,
      account
    })
    if (error) {
      setWithdrawErr(error)
    } else {
      setWithdrawErr(false)
    }
  }

  const updateBalanceAndStake = (amount: any) => {
    const newTokens = [...data.tokens]
    const index = newTokens.findIndex((t: any) => t.address === token.address)
    newTokens[index] = {
      ...token,
      balance: add(token.balance, numberToWei(amount))
    }
    setData({
      ...data,
      tokens: newTokens,
      stake: sub(data.stake, numberToWei(amount))
    })
  }
  const renderWarning = useCallback(() => {
    if (withdrawable instanceof BigNumber && withdrawable.lt(stake))
      return <span className='info'>{getText('withdrawWarining')}</span>
    return null
  }, [withdrawable, stake])
  if (!token) {
    return <div />
  }

  return (
    <Modal visible={visible} setVisible={setVisible}>
      <div className='withdraw-modal'>
        <div className='bank-block'>{getText('bank')}</div>
        <div>
          <ArrowDown />
        </div>
        <div className='content-block'>
          <div className='amount-block'>
            <span>{getText('amount')}:</span>
            <span>
              {getText('available')}:{' '}
              {withdrawable instanceof BigNumber
                ? weiToNumber(
                    withdrawable.lte(0)
                      ? BigNumber.from(0)
                      : withdrawable.gt(stake)
                      ? stake
                      : withdrawable
                  )
                : withdrawable}
            </span>
          </div>
          <div className='input-block'>
            <input
              className='amount-input'
              type='text'
              value={amount}
              onChange={(e) => {
                if (!isNaN(Number(e.target.value))) {
                  setAmount(e.target.value)
                }
              }}
            />
            <button
              className='btn-max'
              onClick={() => {
                const maxAmount =
                  Number(withdrawable) > Number(stake)
                    ? weiToExactNumber(stake)
                    : weiToExactNumber(withdrawable)
                setAmount(maxAmount)
              }}
            >
              {getText('max')}
            </button>
            <div className='token-info'>
              {token && <img src={token.logo} alt={token.symbol} />}
              <span>{token.symbol}</span>
            </div>
          </div>
        </div>
        {renderWarning()}
        <div className='buttons'>
          <Button
            onClick={async () => {
              showLoading(true)
              setSending(true)

              const withdrawSuccess = await withdraw({
                provider,
                stakeTokenAddress: token.address,
                amount: amount || '0',
                data,
                account
              })

              if (withdrawSuccess) {
                updateBalanceAndStake(amount)
                setVisible(false)
              }
              setSending(false)
              showLoading(false)
            }}
            style={{ width: '100%' }}
            disabled={sending || withdrawErr || Number(amount) === 0}
          >
            {withdrawErr || getText('withdraw')}
          </Button>
        </div>
      </div>
    </Modal>
  )
}
