import { ethers } from 'ethers'
import { numberToWei, parseSolidityRevertMessage } from '../utils/helpers'
import { toast } from 'react-toastify'
import { getText } from '../utils/lang'
import _ from 'lodash'
import { abi as abiIERC20 } from '../assets/abis/IERC20.json'
import { abi as abiRouter } from '../assets/abis/UniswapV2Router01.json'
import { abi as abiPair } from '../assets/abis/UniswapV2Pair.json'
import { getConfig } from '../utils/config'

const bn = ethers.BigNumber.from
const ABIs = [abiIERC20, abiRouter, abiPair]
  .reduce((abi: any, a) => abi.concat(a), [])
  .reduce((items: any, item: any) => {
    if (!items.some(({ name }: any) => name === item.name)) {
      items.push(item)
    }
    return items
  }, [])

const iface = new ethers.utils.Interface(ABIs)

export const checkWithdrawError = async ({
  provider,
  stakeTokenAddress,
  amount,
  account,
  data
}: {
  provider: any
  stakeTokenAddress: string
  amount: string
  data: any
  account: string
}) => {
  try {
    amount = numberToWei(amount)
    const rls = await withdrawRLS({
      data,
      token: stakeTokenAddress,
      provider,
      amount,
      account,
      verify: true
    })

    console.log('rls in check error', rls)

    const signer = provider.getSigner()
    const SfarmContract = new ethers.Contract(
      data.contractAddress,
      data.contractABI,
      signer
    )
    await SfarmContract.callStatic.withdraw(stakeTokenAddress, amount, rls)

    return null
  } catch (e) {
    let message = ''
    if (e.data && e.data.message) {
      message = e.data.message
    } else {
      message = e.message || 'Error'
    }
    return message.includes('execution reverted:')
      ? message.slice('execution reverted:'.length)
      : message
  }
}

export const withdraw = async ({
  provider,
  data,
  stakeTokenAddress,
  amount,
  account
}: {
  provider: any
  stakeTokenAddress: string
  amount: string
  account: string
  data: any
}) => {
  try {
    amount = numberToWei(amount)
    const rls = await withdrawRLS({
      data,
      token: stakeTokenAddress,
      provider,
      amount,
      account,
      verify: true
    })
    console.log('rls =', rls)

    const signer = provider.getSigner()
    const SfarmContract = new ethers.Contract(
      data.contractAddress,
      data.contractABI,
      signer
    )

    const txRes = await SfarmContract.functions.withdraw(
      stakeTokenAddress,
      amount,
      rls
    )
    await txRes.wait(1)
    toast.success(getText('withdraw_success'))
    return true
  } catch (e) {
    if (e.data && e.data.message) {
      toast.error(e.data.message)
    } else {
      toast.error(e.message || 'Error')
    }
    return false
  }
}

export const getWithdrawable = async ({
  token,
  data,
  provider
}: {
  token: string
  data: any
  provider: any
}) => {
  const extracted = await pancakeExtract({
    stakeToken: token,
    bankTokenBalances: data.bankTokenBalances,
    bankConfig: data,
    provider
  })

  const best = extracted.liquidable.reduce((best: any, item) => {
    return !best || best.value.lt(item.value) ? item : best
  }, undefined)

  return extracted.balance.add((best?.value ?? bn(0)).mul(99).div(100))
}

export const withdrawRLS = async ({
  data,
  token,
  provider,
  amount,
  account,
  verify
}: {
  data: any
  token: string
  provider: any
  amount: any
  account: any
  verify: boolean
}) => {
  amount = bn(amount)
  const extracted = await pancakeExtract({
    stakeToken: token,
    bankTokenBalances: data.bankTokenBalances,
    bankConfig: data,
    provider
  })

  if (extracted.balance.gte(amount)) {
    return []
  }

  const amountNeeded = amount.sub(extracted.balance)

  // console.log(bns(extracted))
  const liquidable = extracted.liquidable.sort((a, b) => {
    if (a.value.gt(b.value)) return 1
    if (a.value.lt(b.value)) return -1
    return 0
  })

  let firstError
  for (let i = liquidable.length - 1; i >= 0; --i) {
    try {
      // NOTE: typescript transpiler does something spooky here that make return await failed
      const result = await _extractRLS({
        data,
        token,
        provider,
        amount,
        account,
        verify,
        amountNeeded,
        best: liquidable[i]
      })
      return result
    } catch (err) {
      if (!firstError) {
        firstError = err
      }
      console.warn(err)
    }
  }
  throw firstError
}

const _extractRLS = async ({
  data,
  token,
  provider,
  amount,
  account,
  verify,
  amountNeeded,
  best
}: {
  data: any
  token: string
  provider: any
  amount: any
  account: any
  verify: boolean
  amountNeeded: any
  best: any
}) => {
  if (best.value.lt(amountNeeded)) {
    throw new Error(
      `unlockable insufficient: ${best.value.toString()} < ${amountNeeded.toString()}`
    )
  }

  const {
    pair: pairAddress,
    unlocked,
    removeTx,
    unlockTx,
    pairIn,
    tokenOut,
    other
  } = best

  const liquidityNeed = amountNeeded
    .mul(pairIn)
    .mul(1000)
    .div(995)
    .div(tokenOut)

  const rls = []

  if (liquidityNeed.gt(unlocked)) {
    const liquidityMissing = liquidityNeed.sub(unlocked)

    const [tx, receipt] = await Promise.all([
      provider.getTransaction(unlockTx),
      provider.getTransactionReceipt(unlockTx)
    ])

    receipt.logs.forEach((log: any) => {
      try {
        log.parsed = iface.parseLog(log)
        // console.error(log.parsed)
      } catch (err) {
        if (err.reason !== 'no matching event') {
          throw err
        }
      }
    })

    const originalAmount = receipt.logs.find(
      (log: any) =>
        log.address === pairAddress &&
        log.parsed?.name === 'Transfer' &&
        log.parsed?.args.to === data.contractAddress
    ).parsed.args.value

    const originalAmountHex = ethers.utils
      .hexZeroPad(originalAmount.toHexString(), 32)
      .substr(2)
    const amountHex = ethers.utils
      .hexZeroPad(liquidityMissing.toHexString(), 32)
      .substr(2)

    // console.error({originalAmountHex, amountHex, input: tx.data.substring(10+64*4, tx.data.length-56)})
    const exec = {
      router: ethers.utils.getAddress(tx.data.substr(10 + 64 + 24, 40)),
      input:
        '0x' +
        tx.data
          .substring(10 + 64 * 4, tx.data.length - 56)
          .replace(originalAmountHex, amountHex)
    }
    // console.error({tx, receipt, exec})

    if (verify) {
      try {
        const res = await provider.call(
          {
            from: data.contractAddress,
            to: exec.router,
            data: exec.input
          },
          'pending'
        )
        // console.error({res})
        const err = parseSolidityRevertMessage(res)
        if (err) {
          throw new Error(err)
        }
      } catch (err) {
        console.error('farm.withdraw', err)
        throw err
      }
    }

    rls.push({
      receivingToken: getConfig('ZERO_ADDRESS'),
      execs: [exec]
    })
  }

  const tx = await provider.getTransaction(removeTx)

  const routerAddress = ethers.utils.getAddress(
    tx.data.substr(10 + 64 + 24, 40)
  )
  const router = new ethers.Contract(routerAddress, abiRouter, provider)

  if (verify) {
    try {
      const pair = new ethers.Contract(pairAddress, abiPair, provider)
      const allowance = await pair.callStatic.allowance(
        data.contractAddress,
        router.address
      )
      if (allowance.lte(0)) {
        throw new Error(
          `No allowance for ${router.address} to spend ${pairAddress} of SFarm (${data.contractAddress})`
        )
      }
    } catch (err) {
      console.error(
        `${pairAddress}.allowance(${data.contractAddress}, ${router.address})`,
        err
      )
      throw err
    }
    if (rls.length === 0) {
      try {
        await router.callStatic.removeLiquidity(
          token,
          other,
          liquidityNeed,
          0,
          0,
          data.contractAddress,
          getConfig('LARGE_VALUE'),
          { from: data.contractAddress }
        )
      } catch (err) {
        console.error(
          `removeLiquidity(${token}, ${other}) => ${pairAddress}`,
          err
        )
        throw err
      }
    }
  }

  {
    const tx = await router.populateTransaction.removeLiquidity(
      token,
      other,
      liquidityNeed,
      0,
      0,
      data.contractAddress,
      getConfig('LARGE_VALUE'),
      { from: data.contractAddress }
    )
    rls.push({
      receivingToken: token,
      execs: [
        {
          router: router.address,
          input: tx.data
        }
      ]
    })
  }

  const signer = provider.getSigner()
  const SfarmContract = new ethers.Contract(
    data.contractAddress,
    data.contractABI,
    signer
  )

  if (verify) {
    try {
      await SfarmContract.callStatic.withdraw(token, amount, rls, {
        from: account
      })
    } catch (err) {
      console.error(`SFarm.withdraw(${token})`, rls, err)
      throw err
    }
  }

  return rls
}

const pancakeExtract = async ({
  stakeToken,
  bankTokenBalances,
  bankConfig,
  provider
}: {
  stakeToken: string
  bankTokenBalances: any
  bankConfig: any
  provider: any
}) => {
  let removableLPs = bankConfig.bankLiquidity.filter(
    ({ address, txOut }: any) => address === getConfig('ZERO_ADDRESS') && txOut
  )
  await Promise.all(
    removableLPs.map((l: any) =>
      provider
        .getTransactionReceipt(l.txOut)
        .then((receipt: any) => (l.receipt = receipt))
    )
  )
  removableLPs.forEach((l: any) =>
    l.receipt.logs.forEach((log: any) => {
      try {
        log.parsed = iface.parseLog(log)
      } catch (err) {
        if (err.reason !== 'no matching event') {
          throw err
        }
      }
    })
  )

  let removableStakeLPs = removableLPs.filter((l: any) =>
    l?.receipt.logs.some(
      (log: any) =>
        log.parsed?.name === 'Transfer' && log.address === stakeToken
    )
  )

  removableStakeLPs.forEach((l: any) => {
    const transferLogs = l.receipt.logs.filter(
      (log: any) => log.parsed?.name === 'Transfer'
    )
    l.valueLP = transferLogs.find(
      (log: any) =>
        log.address === l.token &&
        log.parsed?.args.from === bankConfig.contractAddress
    ).parsed.args.value
    l.valueToken = transferLogs.find(
      (log: any) =>
        log.address === stakeToken &&
        log.parsed?.args.to === bankConfig.contractAddress
    ).parsed.args.value
    l.other = transferLogs.find(
      (log: any) =>
        log.address !== stakeToken &&
        log.parsed?.args.to === bankConfig.contractAddress
    ).address
    l.valueOther = transferLogs.find(
      (log: any) =>
        log.address !== stakeToken &&
        log.parsed?.args.to === bankConfig.contractAddress
    ).parsed.args.value
  })

  removableLPs = _.keyBy(removableLPs, 'token')
  removableStakeLPs = _.keyBy(removableStakeLPs, 'token')

  let unlockableLPs = bankConfig.bankLiquidity.filter(
    ({ address, token, txIn }: any) =>
      address !== getConfig('ZERO_ADDRESS') && txIn && removableStakeLPs[token]
  )
  unlockableLPs = _.keyBy(unlockableLPs, 'token')

  return {
    balance: bn(bankTokenBalances[stakeToken] ?? 0),
    liquidable: Object.keys(removableStakeLPs).map((pair) => {
      const unlocked = bn(bankTokenBalances[pair] ?? 0)
      const unlockable = bn(unlockableLPs[pair]?.balance ?? 0)
      const removeTx = removableStakeLPs[pair].txOut
      const unlockTx = unlockableLPs[pair]?.txIn
      const pairIn = removableStakeLPs[pair].valueLP
      const tokenOut = removableStakeLPs[pair].valueToken
      const other = removableStakeLPs[pair].other
      const otherOut = removableStakeLPs[pair].valueOther
      const value = unlocked.add(unlockable).mul(tokenOut).div(pairIn)
      return {
        pair,
        unlocked,
        unlockable,
        removeTx,
        unlockTx,
        pairIn,
        tokenOut,
        other,
        otherOut,
        value
      }
    })
  }
}
