import React from 'react'
import Lottie from 'react-lottie'
import loadingData from '../../assets/loading-lottie.json'
import './style.scss'

export default ({ visible }: any) => {
  return (
    <div className={`sfarm-loading ${visible ? '' : 'hidden'}`}>
      <div className='overlay' />
      <div className='loading'>
        {loadingData && (
          <Lottie
            options={{
              loop: true,
              autoplay: true,
              animationData: loadingData,
              rendererSettings: {
                preserveAspectRatio: 'xMidYMid slice'
              }
            }}
            width={200}
            height={200}
          />
        )}
      </div>
    </div>
  )
}
