import React, { useEffect, useRef, useState } from 'react'
import './style.scss'
import { formatFloat, mul, numberToWei, weiToNumber } from '../../utils/helpers'
import {
  getTokenExchangeRate,
  getEarnHistories,
  providerNotSigner
} from '../../service/get-current-state'
import { Line, Chart } from 'test-react-chartjs-2'
import zoomPlugin from 'chartjs-plugin-zoom'
import 'chartjs-adapter-moment'
import { getText } from '../../utils/lang'
import { getEstYield } from '../../service/yield'
import { BigNumber } from 'ethers'

Chart.register(zoomPlugin) // REGISTER PLUGIN

const TIMESTAMP_TO_UPDATE_CHART = 60000

export default ({
  useData,
  onEstYieldAccount
}: {
  useData: any
  onEstYieldAccount?: any
}) => {
  const {
    stake,
    totalEarn,
    totalStake,
    totalContribution,
    harvested,
    lzToken,
    bankApy
  } = useData()
  const [lzExchangeRate, setlzExchagneRate] = useState<any>(null)
  const [earnHistoryData, setEarnHistoryData] = useState<any>({
    chartLabel: [],
    chartData: [],
    earnHistories: null
  })
  const dic = useRef<any>({})

  useEffect(() => {
    fetchLzExchangeRate()
  }, [lzToken])

  useEffect(() => {
    fetchEarnHistoryData()
    setInterval(fetchEarnHistoryData, TIMESTAMP_TO_UPDATE_CHART)
  }, [])

  const fetchLzExchangeRate = async () => {
    if (lzToken) {
      const rate = await getTokenExchangeRate(lzToken.cgkId)
      setlzExchagneRate(rate)
    }
  }

  useEffect(() => {
    if (
      stake == null ||
      bankApy == null ||
      earnHistoryData.earnHistories == null ||
      !dic.current.lastBlock ||
      BigNumber.from(stake).lte(0)
    )
      return
    const handleCalculateEstYield = async () => {
      const { estAccount, lastTimeHarvested } = await getEstYield(
        stake,
        bankApy,
        earnHistoryData.earnHistories,
        dic.current.lastBlock
      )
      onEstYieldAccount && onEstYieldAccount(estAccount, lastTimeHarvested)
    }
    handleCalculateEstYield()
  }, [stake, bankApy, earnHistoryData])

  const fetchEarnHistoryData = async () => {
    const lastBlock = await providerNotSigner.getBlockNumber()
    dic.current.lastBlock = lastBlock
    const res = await getEarnHistories(lastBlock)
    if (res) {
      setEarnHistoryData({
        chartLabel: res.labels,
        chartData: res.data,
        earnHistories: res.earnHistories
      })
    }
  }

  return (
    <div className='bank'>
      <div className='harvest'>
        <p className='title text-gradient font-size-14 mb-05'>
          {getText('total_harvested')}
        </p>
        <p className='value text-default text-weight jc-space-between mb-05'>
          {harvested != null ? (
            <span className='font-size-18'>
              {harvested && weiToNumber(harvested)} LZ
              {lzExchangeRate && harvested ? (
                <span className='ml-1 text-weight-4'>
                  ( $
                  {weiToNumber(mul(harvested, numberToWei(lzExchangeRate)), 36)}{' '}
                  )
                </span>
              ) : (
                ''
              )}
            </span>
          ) : (
            <div className='skeleton-loader__bone width-50' />
          )}
        </p>
      </div>
      <div className='info'>
        <div className='info-row'>
          <span className='text-light'>{getText('deposit')}:</span>
          {totalStake != null ? (
            <span className='text-default'>
              {weiToNumber(totalStake)} {getText('usdz')}
            </span>
          ) : (
            <div className='skeleton-loader__bone width-50' />
          )}
        </div>
        <div className='info-row'>
          <span className='text-light'>{getText('contribution_avg')}:</span>
          {totalContribution != null ? (
            <span className='text-default'>{totalContribution}</span>
          ) : (
            <div className='skeleton-loader__bone width-50' />
          )}
        </div>
        <div className='info-row'>
          <span className='text-light'>{getText('total_harvestable')}:</span>
          {totalEarn != null ? (
            <span className='text-default'>{weiToNumber(totalEarn)} LZ</span>
          ) : (
            <div className='skeleton-loader__bone width-50' />
          )}
        </div>
        <div className='info-row'>
          <span className='text-light'>{getText('apy')}:</span>
          {bankApy != null ? (
            <span className='text-default'>
              {formatFloat(weiToNumber(bankApy), 1)}%
            </span>
          ) : (
            <div className='skeleton-loader__bone width-50' />
          )}
        </div>
      </div>
      <div className='chart'>
        <Line
          type='line'
          className='chart-content'
          data={{
            labels: earnHistoryData.chartLabel,
            datasets: [
              {
                label: 'LZ',
                data: earnHistoryData.chartData,
                fill: false,
                borderColor: '#328B31',
                borderWidth: 1,
                stepped: true,
                spanGaps: true
              }
            ]
          }}
          options={{
            aspectRatio: 2,
            maintainAspectRatio: false,
            elements: {
              point: {
                radius: 0
              }
            },
            scales: {
              y: {
                grid: {
                  color: 'rgba(128,128,128,0.15)'
                }
              },
              x: {
                grid: {
                  color: 'rgba(128,128,128,0.15)'
                },
                type: 'time',
                parsing: false,
                display: true,
                offset: true,
                time: {
                  displayFormats: {
                    day: 'MM/DD'
                  },
                  unit: 'day'
                },
                ticks: {
                  callback: function (value: any, index: any) {
                    return index % 5 === 0 ? value : ''
                  },
                  padding: 0
                }
              }
            },
            plugins: {
              title: {
                display: true,
                text: 'Total Harvestable'
              },
              legend: {
                display: false
              },
              zoom: {
                zoom: {
                  wheel: {
                    enabled: true
                  },
                  mode: 'x',
                  speed: 100
                },
                pan: {
                  enabled: true,
                  mode: 'x',
                  speed: 100,
                  max: 5
                },
                limits: {
                  x: {
                    min: earnHistoryData.chartLabel[0],
                    max: earnHistoryData.chartLabel[
                      earnHistoryData.chartLabel.length - 1
                    ]
                  }
                }
              }
            }
          }}
        />
      </div>
    </div>
  )
}
