import React, { useCallback, useImperativeHandle, useMemo, useRef, useState } from 'react'
import './style.scss'
import { formatFloat, numberToWei, weiToNumber } from '../../utils/helpers'
import Button from '../Button'
import { LockIcon } from '../Icons'
import WarningDialog from '../WarningDialog'

import { harvest } from '../../service/harvest'
import { secondsToContribution } from '../../service/get-current-state'
import { BigNumber } from 'ethers'
import { getText } from '../../utils/lang'
const THRESHOLD_WARNING_UNHARVEST = 3 * 24 * 3600 * 1000
const WalletWrapped = React.forwardRef(
  (
    props: {
      useData: any
      setData: any
      showLoading: any
      useWeb3React: any
    },
    ref
  ) => {
    const [estYieldAccount, setEstYieldAccount] = useState<any>(null)
    const [lastTimeHarvested, setLastTimeHarvested] = useState(null)
    const onEstYieldAccount = useCallback((est, lastTimeHarvested) => {
      setEstYieldAccount(est)
      setLastTimeHarvested(lastTimeHarvested)
    }, [])
    useImperativeHandle(
      ref,
      () => ({
        onEstYieldAccount
      }),
      []
    )
    return <Wallet {...props} estYieldAccount={estYieldAccount} lastTimeHarvested={lastTimeHarvested} />
  }
)
const Wallet = ({
  useData,
  setData,
  showLoading,
  useWeb3React,
  estYieldAccount,
  lastTimeHarvested
}: {
  useData: any
  setData: any
  showLoading: any
  useWeb3React: any
  estYieldAccount: any,
  lastTimeHarvested: any
}) => {
  const { library: provider } = useWeb3React()
  const warningRef = useRef<any>()
  const {
    stake,
    contributionPercent,
    harvestable,
    subsidyRate,
    referralRates,
    contractABI,
    contractAddress,
    lockedSeconds
  } = useData()
  const data = useData()

  const calcHarvestable = () => {
    return harvestable && subsidyRate && referralRates
      ? weiToNumber(
        BigNumber.from(harvestable).mul(
          BigNumber.from(numberToWei(1, 18)) // 100%
            .sub(subsidyRate) // subsidyRate with decimal = 18
            .sub(numberToWei(referralRates[0], 9)) // refRate level 1 with decimal = 9
            .sub(numberToWei(referralRates[1], 9)) // refRate level 2 with decimal = 9
        ),
        36
      )
      : ''
  }
  const calcUnHarvested = ()=>{
    return estYieldAccount && subsidyRate && referralRates
      ? weiToNumber(
        BigNumber.from(numberToWei(estYieldAccount)).mul(
          BigNumber.from(numberToWei(1, 18)) // 100%
            .sub(subsidyRate) // subsidyRate with decimal = 18
            .sub(numberToWei(referralRates[0], 9)) // refRate level 1 with decimal = 9
            .sub(numberToWei(referralRates[1], 9)) // refRate level 2 with decimal = 9
        ),
        36
      )
      : '0'
  }
  const isShowWarning = useMemo(() => {
    if (!lastTimeHarvested) return false
    return new Date().getTime() - lastTimeHarvested > THRESHOLD_WARNING_UNHARVEST
  }, [lastTimeHarvested])
  const timeUnHarvested = useMemo(() => {
    if (!lastTimeHarvested) return false
    return parseInt((new Date().getTime() - lastTimeHarvested) / 1000 / 3600 / 24 + '')
  }, [lastTimeHarvested])
  return (
    <div className='wallet'>
      <div className='content'>
        <div className='info-row'>
          <span className='text-light'>{getText('deposit')}:</span>
          {/* text-gradient invisile when use with display:flex */}
          <span className='text-gradient'>
            {Number(lockedSeconds) > 0 && <LockIcon className='mr-05' />}
            <span className='text-weight-5 mr-05'>{weiToNumber(stake)}</span>
            <span className='font-weight-3'>{getText('usdz')}</span>
          </span>
        </div>
        <div className='info-row'>
          <span className='text-light'>{getText('contribution')}:</span>
          <span className='text-gradient'>
            {contributionPercent
              ? formatFloat(weiToNumber(contributionPercent), 2) + '%'
              : ''}
          </span>
        </div>
        <div className='info-row'>
          <span className='text-light'>{getText('unharvestedEst')}:</span>
          <span className='text-gradient'>{calcUnHarvested()} LZ</span>
        </div>
        {Number(lockedSeconds) > 0 && (
          <div className='info-row'>
            <span className='text-light'>{getText('unlocked_in')}:</span>
            <span className='text-gradient'>
              {secondsToContribution(lockedSeconds)}
            </span>
          </div>
        )}
      </div>
      <div className='harvest'>
        <div className='harvestable text-weight-3'>
          <span className='text-white'>{getText('harvestable')}</span>
          <span className='text-white'>
            <span className='harvestable-value'>{calcHarvestable()}</span>
            <span className='currency'>LZ</span>
          </span>
        </div>
        <Button
          onClick={async () => {
            const handleHarvest = async () => {
              showLoading(true)
              const harvestSuccess = await harvest({
                provider,
                contractABI,
                contractAddress
              })

              if (harvestSuccess) {
                setData({
                  ...data,
                  harvestable: numberToWei('0')
                })
              }
              showLoading(false)
            }

            isShowWarning ? warningRef.current?.handleVisible(true, null, handleHarvest) : handleHarvest()
          }}
          disabled={
            harvestable == null ||
            (harvestable && BigNumber.from(harvestable).isZero())
          }
        >
          {getText('harvest')}
        </Button>
        <WarningDialog
          ref={warningRef}
          message={getText('unHarvestedEstWarning').replace('%d', timeUnHarvested)}
        />
      </div>
    </div>
  )
}
export default WalletWrapped
