import React, { useEffect, useLayoutEffect, useRef, useState } from 'react'
import Bank from './components/Bank'
import TokenAssets from './components/TokenAssets'
import Wallet from './components/Wallet'
import Funds from './components/FundMap'
import './styles/main.scss'
import { fetchData, loadCache } from './service/get-current-state'
import { ToastContainer } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'
import { getText, setLang } from './utils/lang'
import Loading from './components/Loading'
import { getConfig, setupConfig } from './utils/config'

const SUPPORTED_CHAIN_IDS = [56]

export default ({
  theme,
  useWeb3React,
  language,
  xStorageClient,
  env
}: {
  theme: string
  useWeb3React: any
  language?: any
  xStorageClient: any
  env: 'production' | 'development'
}) => {
  const { active, account, library, chainId } = useWeb3React()
  const [data, setData] = useState<any>(loadCache(account))
  const [visibleErrModal, setVisibleErrModal] = useState<boolean>()
  const [loading, setLoading] = useState<boolean>(false)
  const walletRef = useRef<any>()
  const dic = useRef({
    account: undefined
  })
  dic.current.account = account
  useLayoutEffect(() => {
    setupConfig(env)
    fetchData({
      account: getConfig('ZERO_ADDRESS'),
      provider: undefined,
      setData: (data: any) => {
        if (!dic.current.account) {
          setData(data)
        }
      }
    })
  }, [])
  useLayoutEffect(() => {
    if (library && chainId != null && SUPPORTED_CHAIN_IDS.includes(chainId)) {
      // Only fetch data when user is not connect or user connect to network supported
      fetchData({
        account,
        provider: library,
        setData: (data: any, rememberAccount: string) => {
          if (rememberAccount === dic.current.account) {
            setData(data)
          }
        }
      })
    }
    setVisibleErrModal(
      // Show overlay message when user is connect to network unsupported
      chainId != null && !SUPPORTED_CHAIN_IDS.includes(chainId)
    )
  }, [library, account, active, chainId])

  useEffect(() => {
    setLang(language || 'en')
  }, [])

  return (
    <div className={`sfarm-interface ${theme}`}>
      <div className='sfarm-content'>
        <div className='bank-container'>
          <Bank
            onEstYieldAccount={(est: any, lastTimeHarvested: any) => {
              // eslint-disable-next-line no-unused-expressions
              walletRef.current?.onEstYieldAccount(est, lastTimeHarvested)
            }}
            useData={() => data}
          />
        </div>
        <div className='wallet-container'>
          <Wallet
            ref={walletRef}
            showLoading={setLoading}
            useData={() => data}
            setData={setData}
            useWeb3React={useWeb3React}
          />
        </div>
        <TokenAssets
          showLoading={setLoading}
          useData={() => data}
          useWeb3React={useWeb3React}
          setData={setData}
          xStorageClient={xStorageClient}
        />
        <Funds useData={() => data} />
      </div>
      <ToastContainer
        position='top-right'
        autoClose={5000}
        closeOnClick
        rtl={false}
        draggable
      />
      {visibleErrModal && (
        <div className='wrong-network-popup'>
          <div className='error-content'>
            <div className='text-danger'>{getText('connectGuide')}</div>
          </div>
        </div>
      )}
      <Loading visible={loading} />
    </div>
  )
}
