import { ethers } from 'ethers'
import { toast } from 'react-toastify'
import { getText } from '../utils/lang'

export const harvest = async ({
  provider,
  contractABI,
  contractAddress
}: {
  provider: any
  contractABI: any
  contractAddress: string
}) => {
  try {
    const signer = provider.getSigner()
    const SfarmContract = new ethers.Contract(
      contractAddress,
      contractABI,
      signer
    )

    const txRes = await SfarmContract.functions.harvest(0)
    await txRes.wait(1)
    toast.success(getText('harvest_success'))
    return true
  } catch (e) {
    if (e.data && e.data.message) {
      toast.error(e.data.message)
    } else {
      toast.error(e.message || 'Error')
    }
    return false
  }
}
