import React, { useMemo } from 'react'
import './style.scss'
import Button from '../Button'
import { formatFloat, weiToNumber } from '../../utils/helpers'
import { getText } from '../../utils/lang'
const unallocatedIcon =
  'https://raw.githubusercontent.com/ezDeFi/ezdefi-media/master/usdz-logo/usdz-icon.svg'

export default ({ useData }: { useData: any }) => {
  const {
    fundPlatforms,
    tokensInfo,
    fundPlatformUnallocated,
    contractAddress
  } = useData()

  const unallocatedTokenInfo = useMemo(() => {
    if (!fundPlatformUnallocated || !tokensInfo) return []
    const names = fundPlatformUnallocated.names || []
    const infos = names.map((name: any) => {
      const tmp = name.split(',')
      const logo = tokensInfo.find(
        (t: any) => t.symbol.toLowerCase() === tmp[0].toLowerCase()
      )?.logo
      return {
        symbol: tmp[0],
        value: tmp[1],
        logo,
        address: tmp[2]
      }
    })
    return infos
  }, [tokensInfo, fundPlatformUnallocated])
  return (
    <div className='fund-map-container'>
      <div className='title text-gradient font-size-18 font-weight-5'>
        {getText('fund_map')}
      </div>
      <div className='platforms'>
        {fundPlatforms && tokensInfo
          ? fundPlatforms.map((fundPlatform: any, key: number) => {
              const logos = []
              const names = fundPlatform.names
              for (const name of names) {
                const logo = tokensInfo.find(
                  (t: any) => t.symbol.toLowerCase() === name.toLowerCase()
                )?.logo
                logos.push(logo)
              }

              return (
                <div className='platform-box' key={key}>
                  <div className='head'>
                    <div className='logo-and-name'>
                      {fundPlatform.icon && (
                        <img src={fundPlatform.icon} alt='' />
                      )}
                      <span>{fundPlatform.label}</span>
                    </div>
                    <Button
                      disabled={!fundPlatform.apePoolAddress}
                      type='white'
                      size='large'
                      className={
                        fundPlatform.apePoolAddress
                          ? 'view-on-bscscan-btn'
                          : 'view-on-bscscan-btn invisible'
                      }
                      onClick={() => {
                        window.open(
                          'https://bscscan.com/address/' +
                            fundPlatform.apePoolAddress
                        )
                      }}
                    >
                      {getText('view_on_bscscan')}
                    </Button>
                  </div>
                  <div className='platform-content'>
                    <div className='platform-content__row'>
                      <span className='label'>{getText('fund')}</span>
                      <span className='value text-light-green'>
                        {!fundPlatform.fund
                          ? ''
                          : formatFloat(weiToNumber(fundPlatform.fund), 2) +
                            '%'}
                      </span>
                    </div>
                    <div className='platform-content__row'>
                      <span className='label'>{getText('value')}</span>
                      <span className='value'>
                        ${formatFloat(fundPlatform.value, 2)}
                      </span>
                    </div>
                    <div className='platform-content__row'>
                      <span className='label'>{getText('apy')}</span>
                      <span className='value text-green-1'>
                        {!fundPlatform.apy
                          ? ''
                          : formatFloat(weiToNumber(fundPlatform.apy), 1) + '%'}
                      </span>
                    </div>
                    <div className='platform-content__row'>
                      <span className='label'>{getText('quantity')}</span>
                      <span className='value'>
                        {formatFloat(
                          weiToNumber(fundPlatform.depositAmount),
                          2
                        )}
                      </span>
                    </div>
                    <div className='platform-content__row'>
                      <span className='label'>{getText('liquidity')}</span>
                      <span
                        className='value token-pair'
                        onClick={() => {
                          window.open(
                            'https://bscscan.com/address/' +
                              fundPlatform.lpAddress
                          )
                        }}
                      >
                        <div className='pair-token-logo'>
                          {logos.map((logo, key) =>
                            logo ? (
                              <img
                                style={
                                  logos.length > 2
                                    ? {
                                        right: (logos.length - key) * 50 + '%',
                                        position:
                                          logos.length - 1 !== key
                                            ? 'absolute'
                                            : 'relative',
                                        zIndex: logos.length - key
                                      }
                                    : {}
                                }
                                src={logo}
                                alt=''
                                key={key}
                              />
                            ) : null
                          )}
                          <span />
                        </div>
                        {fundPlatform.names.join(' - ')}
                      </span>
                    </div>
                  </div>
                </div>
              )
            })
          : [1, 2, 3].map((_, key) => (
              <div className='platform-box' key={key}>
                <div className='head'>
                  <div className='logo-and-name'>
                    <div className='skeleton-loader__bone flex-1' />
                  </div>
                  <Button
                    disabled
                    type='white'
                    size='large'
                    className='view-on-bscscan-btn invisible'
                  >
                    {getText('view_on_bscscan')}
                  </Button>
                </div>
                <div className='platform-content'>
                  <div className='platform-content__row'>
                    <span className='label flex-1'>{getText('fund')}</span>
                    <div className='skeleton-loader__bone flex-1' />
                  </div>
                  <div className='platform-content__row'>
                    <span className='label flex-1'>{getText('value')}</span>
                    <div className='skeleton-loader__bone flex-1' />
                  </div>
                  <div className='platform-content__row'>
                    <span className='label flex-1'>{getText('apy')}</span>
                    <div className='skeleton-loader__bone flex-1' />
                  </div>
                  <div className='platform-content__row'>
                    <span className='label flex-1'>{getText('quantity')}</span>
                    <div className='skeleton-loader__bone flex-1' />
                  </div>
                  <div className='platform-content__row'>
                    <span className='label flex-1'>{getText('liquidity')}</span>
                    <div className='skeleton-loader__bone flex-1' />
                  </div>
                </div>
              </div>
            ))}
        {fundPlatformUnallocated && (
          <div className='platform-box'>
            <div className='head'>
              <div
                style={{
                  cursor: 'pointer'
                }}
                onClick={() => {
                  window.open(
                    `https://bscscan.com/tokenholdings?a=${contractAddress}`
                  )
                }}
                className='logo-and-name'
              >
                <img src={unallocatedIcon} alt='' />
                <span>{getText('unallocated')}</span>
              </div>
            </div>
            <div className='platform-content'>
              <div className='platform-content__row'>
                <span className='label'>{getText('fund')}</span>
                <span className='value text-light-green'>
                  {!fundPlatformUnallocated.fund
                    ? ''
                    : formatFloat(
                        weiToNumber(fundPlatformUnallocated.fund, 18),
                        1
                      ) + '%'}
                </span>
              </div>
              <div className='platform-content__row'>
                <span className='label'>{getText('value')}</span>
                <span className='value'>
                  {fundPlatformUnallocated.value
                    ? '$' + formatFloat(fundPlatformUnallocated.value, 2)
                    : ''}
                </span>
              </div>
              <div>
                <span className='value'>
                  <div className='pair-token-logo'>
                    <span />
                  </div>
                  <div className='pair-token-box'>
                    {Array.isArray(unallocatedTokenInfo) &&
                      unallocatedTokenInfo.slice(0, 6).map((tk, index) => {
                        if (index === 5 && unallocatedTokenInfo.length > 6) {
                          return (
                            <div key={index} className='pair-token-item'>
                              <span>{getText('others')}</span>
                            </div>
                          )
                        }
                        return (
                          <div
                            onClick={() => {
                              window.open(
                                `https://bscscan.com/token/${tk.address}?a=${contractAddress}`
                              )
                            }}
                            className='pair-token-item'
                            style={
                              index % 2 !== 0
                                ? { justifyContent: 'flex-end' }
                                : {}
                            }
                            key={index}
                          >
                            <span>{`${tk.value}`}</span>
                            <span className='pair-token-symbol'>{`${tk.symbol}`}</span>
                            <img
                              className='pair-token-logo'
                              src={tk.logo}
                              alt=''
                            />
                          </div>
                        )
                      })}
                  </div>
                </span>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  )
}
