import { ethers } from 'ethers'
import { numberToWei } from '../utils/helpers'
import { toast } from 'react-toastify'
import { getText } from '../utils/lang'
import { getConfig } from '../utils/config'
import LZCitizenAbi from '../assets/abis/LZCitizen.abi.json'

export const deposit = async ({
  provider,
  stakeTokenAddress,
  amount,
  newLzReferral,
  contractABI,
  contractAddress
}: {
  provider: any
  stakeTokenAddress: string
  amount: string
  newLzReferral: any
  contractABI: any
  contractAddress: string
}) => {
  try {
    amount = numberToWei(amount)
    const signer = provider.getSigner()
    const SfarmContract = new ethers.Contract(
      contractAddress,
      contractABI,
      signer
    )

    let txRes
    if (newLzReferral) {
      console.log('referAndDeposit', newLzReferral)
      txRes = await SfarmContract.functions.referAndDeposit(
        newLzReferral,
        stakeTokenAddress,
        amount
      )
    } else {
      console.log('deposit without refer')
      txRes = await SfarmContract.functions.deposit(stakeTokenAddress, amount)
    }
    await txRes.wait(1)

    toast.success(getText('deposit_success'))
    return true
  } catch (e) {
    if (e.data && e.data.message) {
      toast.error(e.data.message)
    } else {
      toast.error(e.message || 'Error')
    }
    return false
  }
}

export const getNewReferralAddress = async ({
  xStorageClient,
  account,
  provider
}: {
  xStorageClient: any
  account: string
  provider: any
}) => {
  try {
    if (!xStorageClient) {
      return ''
    }

    const signer = provider.getSigner()
    const CitizenContract = new ethers.Contract(
      getConfig('REFERRAL_CONTRACT_ADDRESS'),
      LZCitizenAbi,
      signer
    )

    const [lzReferral, referral] = await Promise.all([
      xStorageClient
        .get('LZ_REFERRAL')
        .then((address: string) =>
          address ? ethers.utils.getAddress(address) : undefined
        )
        .catch(console.error),
      CitizenContract.functions
        .getReferrer(account)
        .then((r: any) => r[0])
        .catch(console.error)
    ])

    return lzReferral &&
      ethers.utils.isAddress(lzReferral) &&
      lzReferral !== referral
      ? lzReferral
      : ''
  } catch (e) {
    console.error(e)
    return ''
  }
}

export const approveToken = async ({
  provider,
  tokenAddress,
  contractABI,
  contractAddress
}: {
  provider: any
  tokenAddress: string
  contractABI: any
  contractAddress: string
}) => {
  try {
    const signer = provider.getSigner()
    const SfarmContract = new ethers.Contract(tokenAddress, contractABI, signer)

    const txRes = await SfarmContract.functions.approve(
      contractAddress,
      getConfig('LARGE_VALUE')
    )
    await txRes.wait(1)

    toast.success('Approve success')
    return true
  } catch (e) {
    if (e.data && e.data.message) {
      toast.error(e.data.message)
    } else {
      toast.error(e.message || 'Error')
    }
    return false
  }
}

export const checkDepositError = async ({
  provider,
  stakeTokenAddress,
  amount,
  contractABI,
  contractAddress
}: {
  provider: any
  stakeTokenAddress: string
  amount: string
  contractABI: any
  contractAddress: string
}) => {
  try {
    amount = numberToWei(amount)
    const signer = provider.getSigner()
    const SfarmContract = new ethers.Contract(
      contractAddress,
      contractABI,
      signer
    )
    await SfarmContract.callStatic.deposit(stakeTokenAddress, amount)

    return null
  } catch (e) {
    let message = ''
    if (e.data && e.data.message) {
      message = e.data.message
    } else {
      message = e.message || 'Error'
    }
    return message.includes('execution reverted:')
      ? message.slice('execution reverted:'.length)
      : message
  }
}
